  cursor: pointer;
  border-bottom: 2px solid #4b6cc1;
}

/* Media queries */
/* Media queries */
/* Media queries */

/* For Mobiles  */
#check,
.checkbtn {
  display: none;
}
@media (max-width: 860px) {
  .header-main {
    position: -webkit-sticky;
    position: sticky;
    top: 0%;
    padding: 0%;
    /* border: 2px solid white; */
    /* width: auto; */
    width: 100%;
    height: auto;
    /* height: 50px; */
  }
  .checkbtn {
    display: block;
  }
  .checkbtn {
    font-size: 30px;
    color: white;
    position: absolute;
    /* float: right; */
    right: 10px;
    line-height: 80px;
    /* margin-right: 40px; */
    cursor: pointer;
    /* display: none; */
  }
  .header-main ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #12181d;
    top: 60px;
    left: -120%;
    display: flex;
    flex-direction: column;
    text-align: center;
    transition: all 0.5s;
  }
  .header-main nav ul li {
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }
  .header-main nav ul li a {
    font-size: 20px;
  }
  .header-main a:hover,
  a.active {
    background: none;
    color: #0082e6;
  }
  #check:checked ~ ul {
    left: 0;
  }
}

